














import { Component, Ref, Vue } from 'vue-property-decorator';
import { Action } from 'vuex-class';
import { IInputInfo } from '@/types/interface';
import * as commonTypes from '../../types/common';
import banner from '../../components/Banner.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class ForgetPassword extends Vue {
  @Action('saveLoginInfoTemp') saveLoginInfoTemp!: (info: IInputInfo) => void
  @Action('resetLoginInfoMobile') resetLoginInfoMobile!: () => void
  @Action('resetLoginInfoCode') resetLoginInfoCode!: () => void
  @Ref('inputForm') readonly inputForm!: inputFormPanel;

  linkBack() {
    this.$router.push('/');
    this.resetLoginInfoMobile();
    this.resetLoginInfoCode();
  }

  linkNext() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    if (info) {
      this.saveLoginInfoTemp(info);
      this.$router.push('/login/set_new_password');
    }
  }

  inputList = [commonTypes.INPUT_PHONE, commonTypes.INPUT_CAPTCHA];
}
